import React from 'react';
import * as style from './HeaderStyle';
import { Icon } from '@class101/ui';

export default function Header() {
  return (
    <>
      <style.HeaderContainer>
        <style.HeaderBox>
          <style.Logo to="/">CLASS101</style.Logo>
          <style.NavContainer>
            <style.Nav to="/" margin_right="16px">
              
            </style.Nav>
            <style.Nav to="/store"></style.Nav>
          </style.NavContainer>
          <style.SearchForm>
            <style.SearchInput placeholder="  ?" />
            <style.SearchBtn>
              <Icon.Search size={20} />
            </style.SearchBtn>
          </style.SearchForm>
          <style.AuthNavContainer>
            <style.AuthNav to="/creator"> </style.AuthNav>
            <style.AuthNav to="/business"></style.AuthNav>
            <style.AuthNav to="login" margin_right="0px">
              
            </style.AuthNav>
          </style.AuthNavContainer>
        </style.HeaderBox>
      </style.HeaderContainer>
      <style.CategoryContainer>
        <style.CategoryBar>
          <style.CategoryTab margin_right="32px">
             
            <Icon.DropDown size={24} />
          </style.CategoryTab>
          <style.CategoryTab>1  </style.CategoryTab>
          <style.CategoryTab></style.CategoryTab>
          <style.CategoryTab> </style.CategoryTab>
          <style.CategoryTab> </style.CategoryTab>
          <style.CategoryTab> </style.CategoryTab>
          <style.CategoryTab2Container>
            <style.CategoryTab2></style.CategoryTab2>
            <style.CategoryTab2></style.CategoryTab2>
            <style.CategoryTab2> </style.CategoryTab2>
          </style.CategoryTab2Container>
        </style.CategoryBar>
      </style.CategoryContainer>
    </>
  );
}
